:root {
  --md-sys-color-primary: #2196f3;
  --md-sys-color-secondary: #ff4081;
  --background: #f0f0f0;
  --font: #606060;
}

body {
  background: var(--background);
  color: var(--font);
  margin: 0;
  font-family: Helvetica, Arial, sans-serif;
  font-size: 14px;
  display: flex;
}

header {
  background-color: #fff;
  border-radius: 32px;
  width: calc(100% - 48px);
  max-width: 1140px;
  height: 64px;
  margin: 0 auto;
  position: fixed;
  top: 24px;
  left: 0;
  right: 0;
  overflow: hidden;
}

main {
  width: calc(100% - 48px);
  max-width: 1140px;
  margin: 112px auto 0;
}

#instructions {
  width: 50%;
  margin: 64px auto 0;
  display: block;
}

progress-bar {
  z-index: 0;
}

#github-link {
  z-index: 1;
  background-image: url("github-brands-solid-full.a1c4b5e6.svg");
  background-size: cover;
  width: 48px;
  height: 48px;
  position: absolute;
  top: 8px;
  left: 8px;
}

#compute {
  all: unset;
  background-color: var(--md-sys-color-primary);
  color: #fff;
  z-index: 1;
  border-radius: 16px;
  height: 32px;
  padding: 0 8px;
  position: absolute;
  top: 16px;
  right: 16px;
}
